.most-main-div {
  padding-top: 40px;
}

.main-upload-component .custom-control-input:checked~.custom-control-label::before{
  background-color:#1ed660;
}

.main-cntner {
  width: 75%;
  margin: auto;
  /* box-shadow: 3px 3px 10px 0px rgba(50, 50, 50, 0.75); */
  /* box-shadow: 0px 0px 5px 8px rgba(50, 50, 50, 0.14); */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
  border-radius: 10px;
}
.main-rw-row-div {
  padding-top: 10px;
  padding-bottom: 10px;
}
.firstColumn {
  padding-top: 30px;
  border-right: 3px dashed lightgray;
  padding-bottom: 30px;
}
.colum1-title-div {
  font-weight: 600;
  color: gray;
  text-align: center;
}
.secondColumn {
  padding-top: 30px;
}
.colum2-title-div {
  font-weight: 600;
  color: gray;
  text-align: center;
}
.colum2-desc-div {
  padding-top: 20px;
  color: lightgray;
  font-size: 10px;
  text-align: center;
}
.colum2-inpt-div {
  width: 80%;
  margin: auto;
  padding-top: 20px;
}
.colum2-input-with-icon {
  border-radius: 30px;
}
.colum2-input-with-icon:focus {
  box-shadow: none;
  border: 1px solid lightgray;
}
.icon-inpt-colum2 {
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  background-color: #1dd65f;
  color: white;
}
.colum2-icon-dwnload {
  padding-left: 2px;
  padding-right: 2px;
  position: relative;
  right: 2px;
}
.col1-dnd-div {
  padding-top: 20px;
  width: 50%;
  margin: auto;
}
/* .dnd-col1-form {
  min-height: 0px;
  padding: 0px;
  font-size: 13px;
  color: lightgray;
  border: 2px dashed gray;
  border-radius: 3px;
} */
.butn-upld-col1 {
  background: #1dd65f;
  color: white;
  border-radius: 5px;
  padding-right: 15px;
  padding-left: 15px;
}
.butn-upld-col1:focus {
  box-shadow: none;
}
.butn-upld-col1-toggle-div {
  padding-top: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.toggle-switch-col1-btn {
  margin-left: 20px;
}
.thirdColumn {
  padding-top: 30px;
  color: lightgray;
  font-size: 14px;
  text-align: center;
}
.colum2-input-with-icon::placeholder {
  color: lightgray;
  opacity: 1;
  font-size: 10px;
  position: relative;
  bottom: 2px;
  padding-left: 5px;
}
.col1-dnd-div .MuiDropzoneArea-root {
  min-height: auto;
}
.MuiTypography-h5 {
  font-size: unset !important;
}
.col1-dnd-div .MuiDropzoneArea-root {
  outline: none !important;
}
@media only screen and (max-width: 767px) {
  .main-cntner {
    width: 100%;
    margin: auto;
    /* box-shadow: 3px 3px 10px 0px rgba(50, 50, 50, 0.75); */
    /* box-shadow: 0px 0px 5px 8px rgba(50, 50, 50, 0.14); */
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
    border-radius: 10px;
  }
  .firstColumn {
    border-bottom: none;
    border-right: none;
  }
  .butn-upld-col1-toggle-div {
    border-bottom: 3px dashed lightgray;
    padding-bottom: 30px;
  }
}
